/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
  overflow-x: hidden;
}
ol,
ul {
  list-style: none;
}
a:link,
a:visited,
a:hover,
a:active {
  color: currentColor;
  text-decoration: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent; // 뒷 배경을 부모와 같게 맞춰줌
  outline: none;
  box-shadow: none;
  border: none;
}

html {
  font-size: 1rem;
  letter-spacing: -0.1rem;
  @include tablet {
    font-size: 10px;
  }
  @include mobile {
    font-size: 8px;
  }
}
